@mixin fontStyleTitle {
  font-size: 3rem;
  font-family: cursive;
  font-style: italic;    
}

.App {
  text-align: center;
  background: linear-gradient( #bbb, transparent 1px), linear-gradient( 90deg, #bbb, transparent 1px);
  background-size: 20px 20px;
  background-position: center center;
  display: flex;
  min-height: 100vh;
  width: 100vw;
  padding-bottom: 2rem;  
}

.LeftVerticalArea {
  margin-top: 5rem;
  flex-basis: 1rem;
}

.RightVerticalArea {
  flex-basis: calc(100% - 2rem - 1rem); 
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.VerticalTitle {
  @include fontStyleTitle; 
  background-color: transparent;
  text-orientation:upright;
  writing-mode: vertical-lr;
  margin: 0;
  padding: 0;
}

.TitleAndCounters {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 80%;
  align-items: center;
}

.HorisontalTitle {
  @include fontStyleTitle; 
  letter-spacing: 2rem;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}
